<template>
    <div>
        <!-- 面包屑导航区域 -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/index' }">主页</el-breadcrumb-item>
                <el-breadcrumb-item>审批流程</el-breadcrumb-item>
                <el-breadcrumb-item>专案类申请</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card v-if="checkCaseSubHistoryVisible === false">
            <div class="btns">
                <el-row>
                    <el-col :span="5" :offset="9">
                        <h1>审核专案类申请</h1>
                    </el-col>
                    <el-col :span="2" :offset="8">
                        <el-button type="info" icon="el-icon-s-order" @click="openHistory()"> 历史记录 </el-button>
                    </el-col>
                </el-row>
            </div>
            <el-table :data="caseSubList" border>
                <el-table-column prop="caseName" label="专案"></el-table-column>
                <el-table-column prop="subName" label="子流程"></el-table-column>
                <el-table-column prop="description" label="工作描述"></el-table-column>
                <el-table-column prop="applyName" label="申请人"></el-table-column>
                <el-table-column prop="createTime" label="申请时间"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button type="success" @click="judgeApply(scope.row, 1)">通过</el-button>
                        <el-button type="primary" @click="judgeApply(scope.row, 2)">拒绝</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!-- 显示审核记录的图层 -->
        <el-card v-else-if="checkCaseSubHistoryVisible">
            <el-page-header @back="checkCaseSubHistoryVisible = false" content="专案类审核历史记录"></el-page-header>
            <el-table :data="applyHistoryList" style="width: 100%">
                <el-table-column prop="caseName" label="专案">
                </el-table-column>
                <el-table-column prop="subName" label="阶段">
                </el-table-column>
                <el-table-column prop="applyName" label="申请人">
                </el-table-column>
                <el-table-column prop="createTime" label="申请时间">
                </el-table-column>
                <el-table-column prop="checkName" label="审核人">
                </el-table-column>
                <el-table-column prop="checkTime" label="审核时间">
                </el-table-column>
                <el-table-column prop="rejectReason" label="拒绝原因">
                </el-table-column>
                <el-table-column label="结果">
                    <template slot-scope="scope">
                        <el-tag effect="dark" type="success" v-if="scope.row.status === 1">通过</el-tag>
                        <el-tag effect="dark" type="danger" v-else-if="scope.row.status === 2">拒绝</el-tag>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination style="margin-top: 10px;" @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="queryInfo.page" :page-sizes="[5, 8, 10, 15]" :page-size="queryInfo.pageSize"
                layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
import { judgeApply, applyHistoryList } from '@/api/applyCaseSub'
import { mapActions, mapState } from 'vuex'
export default {
    data() {
        return {
            applyList: [],
            checkCaseSubHistoryVisible: false,
            applyHistoryList: [],
            queryInfo: {
                page: 1,
                pageSize: 7
            },
            total: 0
        }
    },
    created() {
        var pageSize = +localStorage.getItem("pim_check_apply_caseSub_pageSize")
        this.queryInfo.pageSize = pageSize == 0 ? 7 : pageSize
        this.getCaseSubApplyList()
    },
    computed: {
        ...mapState(['user']),
        ...mapState('apply', ['caseSubList'])
    },
    methods: {
        ...mapActions('apply',['getCaseSubApplyList']),
        judgeApply(row, status) {
            row.status = status
            row.checkUser = this.user.id
            if (status === 1) {
                this.$confirm('此操作将添加科员到该阶段, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async () => {

                    const res = await judgeApply(row)
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        this.getCaseSubApplyList()
                    } else {
                        this.$message.error(res.msg)
                    }
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '取消操作'
                    });
                });
            } else if (status === 2) {
                this.$prompt('请输入拒绝理由', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(async ({ value }) => {
                    row.rejectReason = value
                    const res = await judgeApply(row)
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        this.getCaseSubApplyList()
                    } else {
                        this.$message.error(res.msg)
                    }
                }).catch((error) => {
                    this.$message({
                        type: 'info',
                        message: '取消操作'
                    });
                });
            }
        },
        openHistory() {
            this.checkCaseSubHistoryVisible = true
            this.getApplyHistoryList()
        },
        async getApplyHistoryList() {
            const res = await applyHistoryList(this.queryInfo)
            if (res.code === 200) {
                this.applyHistoryList = res.data.records
                this.total = res.data.total
            } else {
                this.$message.error(res.msg)
            }
        },
        handleSizeChange(value) {
            localStorage.setItem("pim_check_apply_caseSub_pageSize", value)
            this.queryInfo.pageSize = value
            this.queryInfo.page = 1
            this.getApplyHistoryList()
        },
        handleCurrentChange(value) {
            this.queryInfo.page = value
            this.getApplyHistoryList()
        }
    }
}
</script>

<style scoped></style>